import {useState} from 'react'
import Header from './components/Header'
import Tasks from './components/Tasks'
import AddTask from './components/AddTask'

function App() {
  const [showAddTask, setShowAddTask] = useState(false)
  const [tasks, setTasks] = useState([
    {
        id: 1,
        text: 'Black Friday Shopping',
        day: 'Nov 26th at 2:30pm',
        reminder: true,
    },
    {
        id: 2,
        text: 'Finish Zeitoun Book',
        day: 'Nov 28th at 1:30pm',
        reminder: true,
    },
    {
        id: 3,
        text: 'Math Homework',
        day: 'Nov 30th at 2:30pm',
        reminder: false,
    }
])

// Add Task

const addTask = (task) => {
  const id = Math.floor(Math.random() * 10000) + 1
  const newTask = { id, ...task }
  setTasks([...tasks, newTask])
}

// Delete Task
const deleteTask = (id) => {
  setTasks(tasks.filter((task) => task.id !== id))
}

// Toggle Reminder 

const toggleReminder = (id) => {
  setTasks(tasks.map((task) => task.id === id ? { ...task, reminder: !task.reminder } : task))
}




  return (
    <div className="container">
      <Header onAdd={() => setShowAddTask(!showAddTask)} showAdd={showAddTask}/>
      {showAddTask && <AddTask onAdd={addTask}/>}
      { tasks.length > 0 ? <Tasks tasks={tasks} onDelete={deleteTask} onToggle={toggleReminder}/> : 'No Tasks To Display, Perhaps Drink Some Water 🚰'}
    </div>
  );
}

export default App;
